@charset "UTF-8";
body {
  font-family: "Noto Sans JP", sans-serif;
}

:root {
  --font-weight-regular: 400;
  --font-weight-normal: 500;
  --font-weight-bold: 700;
}

:root {
  --fluid-basis-mobile: (100vw - 340px) / (375 - 320);
  --fluid-basis: (100vw - 768px) / (1440 - 768);
  --fluid-basis-2: (100vw - 1440px) / (1800 - 1440);
}

:root {
  --black: #222;
  --white: #fff;
  --main-color: #0A3158;
  --bg-color: #F1F7FC;
}

body {
  color: var(--black);
}

.u-desktop {
  display: none;
}
@media screen and (min-width: 768px) {
  .u-desktop {
    display: block;
  }
}

@media screen and (min-width: 768px) {
  .u-mobile {
    display: none;
  }
}

/* リキッドレイアウト対応 */
html {
  font-size: 16px;
}
@media (max-width: 375px) {
  html {
    font-size: 4.2666666667vw;
  }
}
@media screen and (min-width: 768px) {
  html {
    font-size: 1.4814814815vw;
  }
}
@media (min-width: 1080px) {
  html {
    font-size: 16px;
  }
}

:root {
  --header-height: 70px;
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

html {
  -webkit-text-size-adjust: 100%;
  text-size-adjust: 100%;
  scroll-behavior: smooth;
}

body {
  margin: 0;
  overflow-wrap: anywhere;
  word-break: normal;
  line-break: strict;
  min-height: 100vh;
  text-rendering: optimizeLegibility;
  line-height: 1.5;
}

ul,
ol,
button,
body,
h1,
h2,
h3,
h4,
p,
figure,
blockquote,
dl,
dd,
time,
small {
  margin: 0;
  padding: 0;
}

ul,
ol {
  list-style: none;
}

a:not([class]) {
  text-decoration-skip-ink: auto;
}

a,
span,
button {
  display: inline-block;
}

small,
time,
figure {
  border: 0;
  font: inherit;
  font-size: 100%;
  vertical-align: baseline;
}

address {
  font-style: normal;
}

img {
  max-width: 100%;
  display: block;
  width: 100%;
}

article > * + * {
  margin-top: 1em;
}

input,
button,
textarea,
select {
  font: inherit;
}

input,
button,
select,
textarea {
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  background: transparent;
  border: none;
  border-radius: 0;
  outline: none;
}

textarea {
  resize: vertical;
}

input[type=checkbox],
input[type=radio] {
  display: none;
}

input[type=submit],
input[type=button],
label,
button,
select {
  cursor: pointer;
}

select::-ms-expand {
  display: none;
}

a {
  text-decoration: none;
  color: inherit;
  transition: 0.3s;
}

button {
  color: inherit;
}

@media (any-hover: hover) {
  a:hover {
    cursor: pointer;
  }
}
@media (min-width: 768px) {
  a[href*="tel:"] {
    pointer-events: none;
    cursor: default;
    text-decoration: none;
  }
}
a {
  -webkit-tap-highlight-color: transparent;
}

button {
  -webkit-tap-highlight-color: transparent;
}

.about {
  margin-top: calc(var(--mv-height) - 3.125rem);
  background-color: var(--white);
  padding-block: 5rem;
  border-radius: 25px 25px 0px 0px;
  position: sticky;
  inset: 0;
  z-index: -1;
}
@media screen and (min-width: 768px) {
  .about {
    margin-top: min(850px, 95vh);
    padding-block: 7.5rem 12.5rem;
    border-radius: 50px 50px 0px 0px;
  }
}

.about__subtitle {
  font-size: 1.25rem;
  font-weight: 700;
  line-height: 1.6;
  letter-spacing: 2px;
  color: var(--main-color);
  margin-top: 1.25rem;
}

.about__text-wrap {
  margin-top: 2.1875rem;
  display: flex;
  flex-direction: column;
  gap: 2.5rem;
}

.about__text {
  font-size: 0.9375rem;
  font-weight: 400;
  line-height: 2;
  letter-spacing: 1.5px;
}
@media (max-width: 1440px) and (max-height: 600px) {
  .about__text {
    line-height: 1.8;
  }
}

.access {
  background-color: var(--bg-color);
  padding-block: 5rem;
  border-radius: 25px 25px 0px 0px;
  margin-top: -25px;
}
@media screen and (min-width: 768px) {
  .access {
    padding-block: 7.5rem;
    border-radius: 50px 50px 0px 0px;
    margin-top: -50px;
  }
}

@media (max-width: 767px) {
  .access__content {
    display: grid;
    grid-template-areas: "title" "map" "note" "btn";
  }
}

@media (max-width: 767px) {
  .access__head {
    display: contents;
  }
}
@media screen and (min-width: 768px) {
  .access__head {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
}

@media (max-width: 767px) {
  .access__title {
    grid-area: title;
  }
}

.access__map-btn {
  color: var(--white);
  font-family: "Josefin Sans", serif;
  font-size: 0.8125rem;
  font-weight: var(--font-weight-regular);
  padding: 0.5rem 2.125rem 0.375rem 2.125rem;
  border: 1px solid var(--main-color);
  background: var(--main-color);
  position: relative;
}
@media (max-width: 767px) {
  .access__map-btn {
    grid-area: btn;
    width: fit-content;
    padding: 0.5rem 2.46875rem 0.375rem 2.46875rem;
    margin-inline: auto;
    margin-top: 1.875rem;
  }
}
@media (any-hover: hover) {
  .access__map-btn:hover {
    background-color: var(--white);
    color: var(--main-color);
  }
}

.access__map-btn::before {
  content: "arrow_outward";
  position: absolute;
  font-family: "Material Symbols Outlined";
  top: 54%;
  translate: 0 -50%;
  right: 0.625rem;
  font-size: 0.875rem;
  font-weight: var(--font-weight-regular);
  color: currentColor;
}

.access__map-wrap {
  margin-top: 2.5rem;
}
@media (max-width: 767px) {
  .access__map-wrap {
    grid-area: map;
  }
}
@media screen and (min-width: 768px) {
  .access__map-wrap {
    margin-top: 5rem;
  }
}

.access__map {
  width: 100%;
  height: 100%;
  aspect-ratio: 330/310;
}
@media screen and (min-width: 768px) {
  .access__map {
    aspect-ratio: 1080/500;
  }
}

.access__note {
  font-size: 0.9375rem;
  font-weight: var(--font-weight-regular);
  letter-spacing: 0.1em;
  margin-top: 1.25rem;
}
@media (max-width: 767px) {
  .access__note {
    grid-area: note;
  }
}
@media screen and (min-width: 768px) {
  .access__note {
    margin-top: 2.5rem;
  }
}

.clients {
  background-color: var(--white);
  padding-block: 5rem;
}
@media screen and (min-width: 768px) {
  .clients {
    padding-block: 7.5rem;
  }
}

.clients__lists {
  display: grid;
  grid-template-columns: 1fr;
  margin-top: 2.5rem;
}
@media screen and (min-width: 768px) {
  .clients__lists {
    grid-template-columns: repeat(2, 1fr);
    gap: 1.875rem;
    margin-top: 5rem;
  }
}

.clients__list {
  display: flex;
  flex-direction: column;
}

.clients__item {
  font-size: 0.9375rem;
  font-weight: var(--font-weight-regular);
  line-height: 2;
  letter-spacing: 0.1em;
}

.company-mv {
  margin-top: var(--header-height);
  height: 368px;
  position: fixed;
  width: 100%;
  inset: 0;
  z-index: -1;
  background: url("../images/company/company-mv-sp.webp") no-repeat top center/cover;
}
@media screen and (min-width: 768px) {
  .company-mv {
    height: 420px;
    background: url("../images/company/company-mv.webp") no-repeat top center/cover;
  }
}

.company-mv__inner {
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding-left: 1.875rem;
}
@media screen and (min-width: 768px) {
  .company-mv__inner {
    padding-left: 11.25rem;
  }
}

.company-mv__en-title {
  color: var(--white);
  font-size: 45px;
  font-weight: 600;
  font-family: "Josefin Sans", serif;
}
.company-mv__en-title::first-letter {
  text-transform: uppercase;
}

.company-mv__jp-title {
  color: var(--white);
  font-weight: 400;
  font-size: 18px;
  letter-spacing: 1.8px;
}
@media screen and (min-width: 768px) {
  .company-mv__jp-title {
    margin-top: 0.3125rem;
  }
}

.contact-form {
  margin-top: calc(var(--header-height) + 332px);
  background-color: var(--white);
  border-radius: 25px 25px 0px 0px;
  padding-bottom: 3.125rem;
}
@media screen and (min-width: 768px) {
  .contact-form {
    margin-top: calc(var(--header-height) + 370px);
    border-radius: 25px 25px 0px 0px;
    padding-bottom: 6.25rem;
  }
}
@media screen and (min-width: 1024px) {
  .contact-form {
    border-radius: 50px 50px 0px 0px;
  }
}

@media screen and (min-width: 768px) {
  .contact-form__inner.inner {
    max-width: 760px;
  }
}

.contact-form__content {
  padding-top: 3.125rem;
}
@media screen and (min-width: 768px) {
  .contact-form__content {
    padding-top: 6.25rem;
  }
}

.contact-form__text {
  font-size: 0.9375rem;
  font-weight: 500;
  line-height: 2;
  letter-spacing: 1.5px;
}

.contact-form__text + .contact-form__text {
  margin-top: 1.25rem;
}

/* ===============================================
# form
=============================================== */
.contact-form__form {
  margin-top: 3.125rem;
}
@media screen and (min-width: 768px) {
  .contact-form__form {
    margin-top: 3.75rem;
  }
}

.wpcf7-form {
  display: flex;
  flex-direction: column;
  gap: 1.25rem;
}
@media screen and (min-width: 768px) {
  .wpcf7-form {
    gap: 1.25rem;
  }
}

.form__group {
  display: flex;
  flex-direction: column;
  gap: 0.3125rem;
}

.form__label {
  font-kerning: none;
  font-feature-settings: "halt" on;
  font-size: 0.9375rem;
  font-weight: 700;
  line-height: 1.8;
  letter-spacing: 0.05em;
  display: flex;
  align-items: center;
  gap: 0.5rem;
}

.form__input {
  font-size: 0.875rem;
  font-weight: 500;
  font-family: "Noto Sans JP", sans-serif;
  padding: 0.75rem 0.9375rem;
  border-radius: 5px;
  border: 1px solid #DDD;
  background: #FDF5EA;
  box-shadow: 0px 2px 2px 0px rgba(0, 0, 0, 0.25) inset;
  width: 100%;
}
.form__input::placeholder {
  color: #7B7B7B;
  opacity: 0.6;
}

.form__textarea {
  height: 11.25rem;
  padding: 0.75rem 0.9375rem;
  border-radius: 5px;
  border: 1px solid #DDD;
  background: #FDF5EA;
  box-shadow: 0px 2px 2px 0px rgba(0, 0, 0, 0.25) inset;
  font-size: 0.875rem;
  font-weight: 500;
  font-family: "Noto Sans JP", sans-serif;
  width: 100%;
}
.form__textarea::placeholder {
  color: #7B7B7B;
  opacity: 0.6;
}
@media screen and (min-width: 768px) {
  .form__textarea {
    height: 12.5rem;
  }
}

.wpcf7-spinner {
  display: none;
}

.form__submit-btn {
  max-width: 20.625rem;
  margin-inline: auto;
  width: 100%;
  margin-top: 0.625rem;
}
@media screen and (min-width: 768px) {
  .form__submit-btn {
    margin-top: 1.25rem;
  }
}

.form__submit-button {
  color: #FFF;
  font-size: 0.875rem;
  font-weight: 700;
  line-height: 1.5;
  display: flex;
  width: 100%;
  height: 3.4375rem;
  align-items: center;
  justify-content: center;
  background: var(--main-color);
  box-shadow: 0px 2px 4px 0px rgba(0, 0, 0, 0.25);
  border: 1px solid transparent;
  transition: color 0.3s ease, background 0.3s ease, border 0.3s ease;
}
@media (any-hover: hover) {
  .form__submit-button:hover {
    background: var(--white);
    color: var(--main-color);
    border: 1px solid var(--main-color);
  }
}
@media screen and (min-width: 768px) {
  .form__submit-button {
    font-size: 1rem;
    height: 3.75rem;
  }
}

.form__group.form__group--textarea {
  gap: 0;
}
.form__group.form__group--textarea .form__label {
  margin-bottom: 0.625rem;
}

.contact {
  padding-block: 3.4375rem;
  background-color: var(--white);
  position: relative;
}
@media screen and (min-width: 768px) {
  .contact {
    padding-block: 7.5rem;
  }
}

.contact::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: var(--bg-color);
  border-radius: 0px 25px 0px 0px;
}
@media screen and (min-width: 768px) {
  .contact::before {
    border-radius: 0px 50px 0px 0px;
    width: calc(100% - 50px);
  }
}

.contact__content {
  position: relative;
  z-index: 1;
  display: flex;
  align-items: center;
}
@media (max-width: 767px) {
  .contact__content {
    flex-direction: column;
    gap: 2.25rem;
  }
}
@media screen and (min-width: 768px) {
  .contact__content {
    justify-content: space-between;
  }
}

.contact__link {
  padding-inline: 5.6875rem 5.1875rem;
  height: 4.375rem;
  background-color: var(--main-color);
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 55px;
  color: var(--white);
  font-size: 0.875rem;
  font-weight: 500;
  letter-spacing: 1.4px;
  border: 2px solid var(--main-color);
  width: fit-content;
  transition: background-color 0.5s, color 0.5s;
}
@media (min-width: 768px) and (max-width: 1260px) {
  .contact__link {
    margin-right: 3.75rem;
  }
}
@media (any-hover: hover) {
  .contact__link:hover {
    background-color: var(--white);
    color: var(--main-color);
  }
  .contact__link:hover .contact__link-text::after {
    border-color: var(--main-color);
  }
  .contact__link:hover .contact__link-text::before {
    background: url("../images/common/mail-icon-blue.svg") no-repeat center center/contain;
  }
}

.contact__link-text {
  position: relative;
}

.contact__link-text::before {
  content: "";
  position: absolute;
  top: 50%;
  translate: 0 -50%;
  left: -2.5rem;
  background: url("../images/common/mail-icon.svg") no-repeat center center/contain;
  width: 1.6875rem;
  height: 1.25rem;
  transition: background 0.3s;
}

.contact__link-text::after {
  content: "";
  position: absolute;
  top: 50%;
  right: -65%;
  width: 8px;
  height: 8px;
  border-top: 2px solid var(--white);
  border-right: 2px solid var(--white);
  transform: translateY(-50%) rotate(45deg);
  transition: border-color 0.3s;
}

.error {
  padding-block: 6.25rem 1.875rem;
}
@media screen and (min-width: 768px) {
  .error {
    padding-block: 9.375rem 3.125rem;
  }
}

.error__text {
  text-align: center;
  line-height: 2.5;
  font-weight: 500;
  font-size: 0.8125rem;
}
@media screen and (min-width: 768px) {
  .error__text {
    font-size: 1.25rem;
  }
}

.error__btn {
  text-align: center;
}
@media (max-width: 767px) {
  .error__btn {
    margin-bottom: 3.125rem;
  }
}

.error__btn-link {
  padding: 1.25rem 5rem;
  background-color: var(--main-color);
  color: var(--white);
  border-radius: 100vmax;
  font-size: 0.875rem;
  font-weight: 500;
  line-height: 1;
  letter-spacing: 1.4px;
  position: relative;
  border: 2px solid var(--main-color);
  transition: color 0.3s, background-color 0.3s;
  margin-top: 1.875rem;
}
@media (max-width: 767px) {
  .error__btn-link {
    width: fit-content;
    order: 3;
    margin-top: 0.625rem;
    margin-inline: auto;
    padding: 0.9375rem 3.75rem;
  }
}
@media (any-hover: hover) {
  .error__btn-link:hover {
    color: var(--main-color);
    background-color: var(--white);
  }
  .error__btn-link:hover::after {
    border-top: 2px solid var(--main-color);
    border-right: 2px solid var(--main-color);
  }
}

.error__btn-link::after {
  content: "";
  position: absolute;
  top: 50%;
  right: 1.25rem;
  width: 8px;
  height: 8px;
  border-top: 2px solid var(--white);
  border-right: 2px solid var(--white);
  transform: translateY(-50%) rotate(45deg);
  transition: border-color 0.3s;
}

.inner {
  width: 100%;
  padding-right: 30px;
  padding-left: 30px;
  margin-right: auto;
  margin-left: auto;
  max-width: 500px;
}
@media screen and (min-width: 768px) {
  .inner {
    max-width: 1150px;
    padding-right: 35px;
    padding-left: 35px;
  }
}

#loading-wrapper {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: white; /* 背景を白に */
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 9999;
}

#lottie-container {
  width: 100%;
}
@media screen and (min-width: 768px) {
  #lottie-container {
    width: 50%;
  }
}

.mv {
  padding-top: var(--header-height);
  max-height: 650px;
  position: fixed;
  width: 100%;
  inset: 0;
  z-index: -1;
}
@media screen and (min-width: 768px) {
  .mv {
    max-height: min(900px, 100vh);
    overflow: hidden;
  }
}

.mv__inner {
  height: 100%;
  position: relative;
}

.mv__content {
  position: absolute;
  z-index: 10;
  bottom: 0;
  margin-bottom: 8.6875rem;
}
@media (max-width: 767px) {
  .mv__content {
    left: 50%;
    translate: -50% 0;
  }
}
@media screen and (min-width: 768px) {
  .mv__content {
    margin-left: 10.8125rem;
    margin-bottom: 10.625rem;
    left: 0;
  }
}
@media (min-width: 768px) and (max-height: 650px) {
  .mv__content {
    margin-bottom: 3.75rem;
  }
}
@media (min-width: 768px) and (max-width: 1440px) and (max-height: 600px) {
  .mv__content {
    zoom: 0.8;
    margin-bottom: 6.25rem;
  }
}

.mv__title {
  color: var(--white);
  font-size: 1.75rem;
  font-weight: 600;
  line-height: 1.3;
  white-space: nowrap;
}
@media (max-width: 767px) {
  .mv__title {
    text-align: center;
  }
}
@media screen and (min-width: 768px) {
  .mv__title {
    font-size: 2.8125rem;
    line-height: 70px;
  }
}

.mv-text-animation {
  overflow: hidden;
}

.mv-text-animation span {
  display: inline-block;
  opacity: 0;
  transform: translateY(1em);
  animation: none; /* 初期状態ではアニメーションなし */
}

.animate-text .mv-text-animation span {
  animation: fadeUp 0.5s ease-out forwards;
  animation-delay: calc(var(--delay, 0s) + var(--index) * 0.05s);
}

.mv-text-animation.delay-1 span {
  animation-delay: calc(0.3s + var(--index) * 0.02s);
}

@keyframes fadeUp {
  from {
    opacity: 0;
    transform: translateY(1em);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}
.mv__text {
  color: var(--white);
  font-size: 0.625rem;
  font-family: "Crimson Text", serif;
  font-weight: 400;
  line-height: 32px;
  letter-spacing: 1px;
  white-space: nowrap;
}
@media screen and (min-width: 768px) {
  .mv__text {
    font-size: 1.125rem;
    margin-top: 2.125rem;
    letter-spacing: 1.8px;
  }
}

.mv__link {
  height: 4.375rem;
  background-color: var(--main-color);
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 55px;
  color: var(--white);
  width: 16.875rem;
  font-size: 0.875rem;
  font-weight: 500;
  letter-spacing: 1.4px;
  position: relative;
  transition: color 0.3s, background-color 0.3s;
  margin-top: 5.3125rem;
  opacity: 0;
  border: 2px solid var(--main-color);
}
@media screen and (max-height: 650px) {
  .mv__link {
    margin-top: clamp(30px, 10vh, 85px);
  }
}
@media (max-width: 767px) {
  .mv__link {
    margin-inline: auto;
    animation-delay: 0.7s;
  }
}
@media screen and (min-width: 768px) {
  .mv__link {
    margin-top: 3.75rem;
  }
}
