  background-clip: text;
  background-image: linear-gradient(80deg, var(--main-color) 33.33%, #0038B5, #00FFFF, #002aff, #049bff, #00FFFF, rgba(255, 255, 255, 0) 60%);
  background-position: right center;
  background-size: 300%;
  color: transparent;
}
.section-title.js-title-animation.is-active {
  animation: txtPika 1.5s ease-in-out forwards;
}

@keyframes txtPika {
  0% {
    background-position: 100% 0;
    -webkit-background-clip: text;
  }
  100% {
    background-position: 0 0;
    -webkit-background-clip: text;
  }
}
.pagination {
  display: flex;
  justify-content: center;
  gap: 0.3125rem;
}

.page {
  display: flex;
  width: 32px;
  height: 32px;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-color: var(--white);
  font-size: 0.8125rem;
  color: #333;
  border-radius: 8px;
  font-family: "Open Sans", serif;
  font-weight: 600;
  border: 1px solid #DFDFDF;
  transition: background-color 0.3s, color 0.3s;
}
.page.current {
  background-color: var(--main-color);
  color: var(--white);
}
@media (any-hover: hover) {
  .page:hover {
    background-color: var(--main-color);
    color: var(--white);
  }
}

.dots {
  display: flex;
  width: 32px;
  height: 32px;
  justify-content: center;
}

.section-title {
  font-family: "Josefin Sans", serif;
  color: var(--main-color);
  font-size: 2.5rem;
  font-weight: 500;
  letter-spacing: 1.2px;
  line-height: 1;
}
.section-title::first-letter {
  text-transform: uppercase;
}
@media screen and (min-width: 768px) {
  .section-title {
    font-size: 3.125rem;
    letter-spacing: 1.5px;
  }
}